import React from "react";
import { StaticQuery, graphql } from "gatsby";
import Img from "gatsby-image";
import { navigate } from "gatsby-link";

import SectionWrapper from "components/sectionWrapper/sectionWrapper";
import Title from "components/title";

import {
  StyledAbout,
  StyledTextBox,
  StyledFlexItem,
  StyledButtonAbout,
} from "./about.styles";

const About = () => {
  return (
    <StaticQuery
      query={graphql`
        query {
          file(relativePath: { eq: "about-pic.jpg" }) {
            childImageSharp {
              fluid {
                ...GatsbyImageSharpFluid_noBase64
              }
            }
          }
        }
      `}
      render={(data) => (
        <SectionWrapper>
          <StyledAbout id="about">
            <StyledFlexItem data-sal="slide-up" data-sal-duration="500">
              <Img fluid={data.file.childImageSharp.fluid} />
            </StyledFlexItem>

            <StyledFlexItem data-sal="slide-up" data-sal-duration="500">
              <Title title="O byciu psycholok i psychoterapeutk" />

              <StyledTextBox>
                <p>
                  Wierz, e proces terapeutyczny to najlepsza forma poznania
                  siebie i bezpiecznej pracy nad zrozumieniem relacji, ktre
                  budujemy z innymi ludmi. Jako psycholoka i psychoterapeutka
                  w zgbianiu rozumienia psychiki ludzkiej kad szczegln
                  uwag na rol ciaa i ruchu. Zarwno w kontakcie
                  indywidualnym, jak i grupowym korzystam z poznanych technik
                  pracy terapeutycznej i rosncego dowiadczenia. Z ogromn
                  pasj odnosz si do swojego zawodu. Swoich pacjentw zawsze
                  staram si obdarza penym szacunkiem oraz empati,
                  zapewniajc im maksymalne wsparcie i pen dyskrecj.
                </p>
              </StyledTextBox>

              <StyledButtonAbout onClick={() => navigate("/about")}>
                Wicej o mnie
              </StyledButtonAbout>
            </StyledFlexItem>
          </StyledAbout>
        </SectionWrapper>
      )}
    />
  );
};

export default About;
